@import url(https://fonts.googleapis.com/css?family=Roboto|Roboto+Mono&display=swap);
body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

body{font:16px/1.5 "Roboto", sans-serif;margin:0;padding:0}@media only screen and (min-width: 900px){body{font-size:18px}}h1,h2,h3,h4,h5,h6{line-height:1.25;margin:0;padding:0.75rem 0 0.5rem 0}h1{border-bottom:1px solid #d0d3df}img{margin-top:1rem;max-width:100%}a{color:#2962ff;text-decoration:none;-webkit-transition:color 250ms ease-in-out;transition:color 250ms ease-in-out}a:hover{color:#82b1ff}pre,code{background-color:#d0d3df;padding:0 0.25rem;font:14px "Roboto Mono", monospace}pre{border-radius:0.5rem;padding:0.5rem}blockquote{border-left:4px solid #81838e;padding-left:1rem;margin:0}#main{box-sizing:border-box;width:100%;min-height:100vh;display:flex;flex-direction:column}@media only screen and (min-width: 900px){#main{flex-direction:row}}.container{background:#fff;display:flex;flex-direction:column;padding:0.5rem;border-bottom:1px solid #d0d3df;box-sizing:border-box;height:50vh;width:100%}@media only screen and (min-width: 900px){.container{padding:1rem;border-right:1px solid #d0d3df;height:100vh;border-bottom:0}}.header{text-transform:uppercase;color:#81838e;font-size:14px;padding-bottom:0.25rem}@media only screen and (min-width: 900px){.header{font-size:16px;padding-bottom:0.5rem}}#editor{border:1px solid #81838e;font-family:"Roboto Mono", monospace;border-radius:2px;padding:0.5rem 1rem 0.5rem 0.5rem;width:100%;font-size:16px;height:100%;display:flex;box-sizing:border-box;resize:none}@media only screen and (min-width: 900px){#editor{font-size:18px}}#preview-container{border:0}#preview{overflow-y:scroll;padding-right:1rem;height:100%}

